  .container__body {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .content {
    /* width: 140px; */
  }
}
@media (min-width: 970px) {
  .container__body {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .content {
