/* Position the label over the input field */
.relative__form__group {
  position: relative;
}
.absolute__form__lable {
  position: absolute;
}

/* Hide the label border when the input field has text */
:not(:placeholder-shown) + label {
  top: 0.75rem;
  left: 1.5rem;
  font-weight: 600;
  font-size: 1rem;
  color: #374151;
  border: none;
}

/* Move the label up and reduce font size on focus */
.relative__form__group:focus-within label {
  transform: translateY(-90%) scale(0.75);
  transition: all 0.3s ease;
}

/* Hide the placeholder text on focus */
.relative__form__group:focus-within input::placeholder {
  opacity: 0;
}

/* Restore the label position when the input field loses focus */
.relative__form__group input:not(:focus):not(:placeholder-shown) + label {
  top: 0;
  left: 1.5rem;
  font-size: 0.75rem;
  font-weight: 600;
  color: rgb(123, 123, 123);
  border: none;
  transform: translateY(-90%);
  transition: all 0.3s ease;
}
